
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    font-size: 12px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

nav{
  padding: 0 1rem; /*сверху-снизу, справа-слева */
}

.content{
  min-height: calc(100vh - 64px - 21px); /*вычтем высоту хэдэра и футера, чтобы прижать футер к низу*/
  padding: 1.2rem 0;
} 

.goods{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1rem;
  
}

.card-title{
  font-size: 0.6rem;
}

.buy-btn{

  margin-right: 35px;

}
.price{
  font-size: 1.1rem;
}

.buy-action{
  display: flex;
  justify-content: space-between;
  height: 50px;
  position: absolute;
  bottom: 0;
}

.card{
  display: flex;
  flex-direction: column;
  position: relative;

}

.card-content{
  flex-grow: 1;
  margin-bottom: 40px;
}


.cart{
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  cursor: pointer;
  z-index: 5;
  padding: 1rem;
}
@media (min-width: 767px){
  .cart{
    top: 5rem;
    bottom: unset; /* наследует от 1 варианта */
  }
}

.basket-list{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  width: 50rem;
  max-width: 95%;
  box-shadow: 4px 4px 37px 0px rgba(34, 60, 80, 0.2);

  animation: show 500ms ease-in-out;

}
.basket-close{
  cursor: pointer;
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  color: #fff;
}
.order-delete{
  cursor: pointer;
  color: rgb(0 96 100);
}

 @keyframes show { /*анимация появления корзины*/
  from{ 
    top: 70%;
    opacity: 0;
  }
  to{
    top: 50%;
    opacity: 1;
  }
}

.order-btn{
  display: inline-block;
  margin-left: 78%;
  margin-top: -45px;
}
.total{
  border: 3px solid rgb(0 96 100);
  border-radius: 5px;
  padding: 7px;
}
.basket-quantity{
  cursor: pointer;
  vertical-align: middle;
  color: rgb(0 96 100);
}

#toast-container{
  top: 3rem;
  animation: fade-in 250ms ease-in-out;
}
@keyframes fade-in { /*анимация появления подсказки*/
  from{
    opacity: 0;
    transform: translateY(2rem);
  }
  to{
    opacity: 1;
    transform: translateY(0rem);
  }
}


